.popup__modal__feedback {
  background-color: rgba(255, 255, 255, 0.5);
  width: 100vw;
  height: 680px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .wrapper__modal__feedback {
    display: flex;
    flex-direction: column;
    background-color: $wight;
    width: 320px;
    height: 676px;
    padding: 16px;
    margin-left: auto;
    box-shadow: -20px 0px 35px -5px $rgba;
  }

  .popup__modal__feedback__clouse__btn {
    width: 40px;
    height: 40px;
    background: url('../src/img/clouse.png');
    border: none;

    &:hover {
      cursor: pointer;
    }
  }

  .popup__modal__feedback__title {
    font-weight: 700;
    font-size: 24px;

    &::before {
      content: '';
      display: block;
      position: relative;
      left: -21px;
      top: 26px;
      width: 5px;
      height: 40px;
      border-right: 5px solid $light_green;
      border-radius: 15px;
    }
  }

  .popup__modal__feedback__form {
    padding-top: 24px;

    .input {
      width: 288px;
      height: 48px;
      border: 1px solid $light_light_grey;
      border-radius: 10px;
      padding-left: 16px;
      margin-bottom: 16px;

      &::placeholder {
        color: $grey_input;
        font-weight: 500;
        font-size: 16px;
      }
    }

    .popup__modal__feedback__textarea {
      resize: none;
      height: 118px;
      width: 288px;
      border: 1px solid $light_light_grey;
      border-radius: 10px;
      padding-left: 16px;
      padding-top: 16px;

      &::placeholder {
        color: $grey_input;
        font-weight: 500;
        font-size: 16px;
      }
    }

    .popup__modal__feedback__text {
      margin-top: 16px;
      font-weight: 500;
      font-size: 14px;
      padding-bottom: 18px;

      .popup__modal__feedback__link {
        text-decoration: none;
        color: $red;
      }
    }

    .popup__modal__feedback__send__btn {
      position: relative;
      border: none;
      background-color: $redBtn;
      color: $wight;
      width: 136px;
      height: 32px;
      font-weight: 700;
      font-size: 10px;
      border-radius: 20px;
      margin-left: 152px;
      top: 8px;
      padding-right: 20px;
      padding-top: 3px;

      &:hover {
        cursor: pointer;
      }

      &::after {
        content: '';
        position: absolute;
        display: block;
        width: 5px;
        height: 5px;
        color: $wight;
        border-top: 2px solid $wight;
        border-right: 2px solid $wight;
        transform: rotate(45deg);
        right: 16px;
        bottom: 12px;
      }
    }
  }
}

.popup__modal__feedback--hidden {
  display: none;
}

@media (min-width: 768px) {
  .popup__modal__feedback {
    height: 940px;

    .wrapper__modal__feedback {
      position: relative;
      width: 440px;
      height: 940px;
      padding: 0 32px;
    }

    .popup__modal__feedback__clouse__btn {
      position: absolute;
      left: -74px;
      top: 25px;
    }

    .popup__modal__feedback__title {
      display: inline;
      margin: 0;
      padding: 0;

      &::before {
        left: -38px;
      }
    }

    .popup__modal__feedback__form {
      .input {
        width: 376px;
      }

      .popup__modal__feedback__send__btn {
        margin-left: 242px;
      }
    }
  }
}
